<template>
	<view>
		<view class="kang">
			<span class="zhi">上传身份证明</span>
		</view>
		
		<view class="jun">
			<view class="jia">
			<view class="ye">请上传</view>
			<view class="zi">执业证,资格证,医院工作证,胸牌均可作为</view>
			<view class="ping">凭证。</view>
			</view>
		</view>
		
		<view class="default"　v-mode="chooseHead"　@tap="chooseHeadUrl">上传图片</view>
		<button >跳转</button>
		<button v-mode="uploadImage" @tap="uploadImageUrl">上传</button>
	</view>
</template>

<script>
import {uploadMessage} from 'API/message.js'
import {getSTS} from 'API/getSTS.js' 

	export default {
		onLoad: function (option) {
		    this.userData2 = JSON.parse(decodeURIComponent(option.userData1));
		},
		data() {
			return {
					chooseHead:'',
					uploadImage:'',
					portraitUri:'',
					userData2:{}
				}
			},
		methods: { 
	//选择图片
	   chooseHeadUrl() {
	    let that = this
	    uni.chooseImage({
	     count: 1, //默认9
	     sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
	     success: function(res) {
	      console.log(JSON.stringify(res.tempFilePaths));
	      that.portraitUri = res.tempFilePaths[0]
		  console.log(that.portraitUri,'999')
	     }
	    })
	   },
	   //上传图片到OSS
	   uploadImageUrl(item) {
		   let that = this
	    if(this.portraitUri != ''){
	     let that = this
	     getSTS()
	      .then((res) => {
	       // 向后台发请求 拉取OSS相关配置
	       const creds = res[1].data.data;
	       console.log(creds,'666')
	       
	       //随机数
	       function randomString(length) {
	        var str = "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";
	        var result = "";
	        for (var i = length; i > 0; --i) {
	         result += str[Math.floor(Math.random() * str.length)];
	        }
	        return result+(new Date()).valueOf();
	       }
		   let date = new Date ()
		   let year = date.getFullYear()
		   let month = date.getMonth()+1
		   if(month<9){
			   month = '0'+ month
		   }
		   let time = year +'-'+ month +'/'
		   console.log(time,'time')
	       const SJS =time+ randomString(6);
			console.log(that.portraitUri,'that url')
	       uni.uploadFile({
	        url: creds.host, // 开发者服务器的URL。
	        filePath: that.portraitUri,
	        name: 'file', // 必须填file。 
	        formData: {
	         key: creds.dir + SJS + ".jpg",
	         policy: creds.policy,
	         OSSAccessKeyId: creds.accessid,
	         signature: creds.signature,
	         // 'x-oss-security-token': securityToken // 使用STS签名时必传。
	        },
	        success: (res) => {
				console.log(res)
				if(res.statusCode == 204){
					let obj1 = {
							speciality:this.text,
							identificationPaper:that.chooseHead,
							portraitUri:that.portraitUri,
						};
					let obj2 = that.userData2; 
					let obj3 = { ...obj1,...obj2 };
					console.log(obj3,55555)
					uploadMessage(obj3).then(res=>{ 
							console.log(res) 
							uni.navigateTo({
								url:'../tabbar/index/index', //请求成功后跳转首页
								});
						}) 
				}else {
					uni.showToast({
						title:'上传图片失败'
					})
				}
				// console.log(this.nameIdx,this.sexIdx,this.date)
				
	        },
	        fail: err => {
	         console.log(err);
	        }
	       });
	      })
	      .then((res) => {
	       // if (this.tableId == 0) {
	       //  this.food.imageUrl = res.url;
	       // } else if (this.tableId == 1) {
	       //  this.foodMenu.imageUrl = res.url;
	       // }
	      })
	      .catch((err) => {
	       console.log(err);
	      });
	    }
	    
	   },
	   },
	   }
</script>

